import React from 'react';
import renouvellementAssociatif from '../Assets/RenouvellementAssociatif.png';
import vieAssociative from '../Assets/VieAssociative.png';
import UA_logo from '../Assets/Logos/UA_Logo.png';
import FlecheDroite from "../Assets/FlecheDroite.svg";
import "./ImageDeGarde.css"

export default class ImageDeGarde extends React.Component{
    constructor(props){
        super(props);
        if (this.props.Page === "Accueil") {
            this.ImageDeGarde = renouvellementAssociatif;
            this.Title = <h1 id={"TitreDebutDePage-h1"}> Union des Associations <br/> de l’École centrale de Marseille </h1>
            this.SubTitle = <h2 id={"TitreDebutDePage-h2"}> Fédération associative </h2>
        }
        else if (this.props.Page === "VieAssociative") {
            this.ImageDeGarde = vieAssociative;
            this.Title = <h1 id={"TitreDebutDePage-h1"}> Vie Associative </h1>
            this.SubTitle = <h2 id={"TitreDebutDePage-h2"}> 40+ associations <br/> 6 pôles thématiques </h2>
        }
    }

    render() {

        return(
            <div id="ArriveeAccueil-Container">
                <img src={this.ImageDeGarde} style={{width: "100%"}} alt={"Image de garde de la page " + this.props.Page}/>
                <div id={"TitreDebutDePage-grid"}>
                    <img id="TitreDebutDePage-UAlogo" src={UA_logo} width={200} alt={"Logo Union des Associations"}/>
                    <div id={"TitreDebutDePage-texte"}>
                        {this.Title}
                        <img src={FlecheDroite} style={{margin: "5px"}} width={500}/>
                        {this.SubTitle}
                    </div>
                </div>
            </div>
        )
    }
}